.my__footer {
    border: 1px solid whitesmoke;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.my__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.my__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}

.my__footer-links {
    /* display: flex */
    display: none;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.my__footer-links div {
    width: 250px;
    margin: 1rem
}

.my__footer-links__logo {
    display: flex;
    flex-direction: column;
}

.my__footer-links__logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}

.my__footer-links__logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

.my__footer-links__div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.my__footer-links__div h4 {
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 16px;
    color: #FFFFFF;
    margin-bottom: 1rem;
}

.my__footer-links__div p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 14px;
    color: #FFFFFF;
    margin: 0.5rem 0;
    cursor: pointer;
}

.my__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.my__footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 14px;
    color: #FFFFFF;
}

.my__footer-btn {
    display: none;
}

@media screen and (max-width: 850px) {
    .my__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .my__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .my_footer-links__div {
        margin: 1rem 0;
    }
}

@media screen and (max-width: 400px) {
    .my__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
