.my__header {
    display: flex;
    flex-direction: row;
}

.my__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.my__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.my__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.my__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    /* display: flex; */
    display: none;
    flex-direction: row;
}

.my__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.my__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: #FF4820;
    color: #FFFFFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
    

.my__header-content__people {
    width: 100%;
    /* display: flex; */
    display: none;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.my__header-content__people img{
    width: 180px;
    height: 40px;
}

.my__header-content__people p {
    font-family: var(--font-family);
    font-weight: 500px;
    font-size: 24px;
    line-height: 38px;
    color: #FFFFFF;
    margin: 0 0 0 1rem;
    text-align: center;
}

.my__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.my__header-image img{
    width: 100%;
    height: 100%;
}


@media screen and (max-width: 1050px) {
    .my__header {
        flex-direction: column;
    }

    .my__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .my__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .my__header-content p {
        font-size: 16px;
        line-height: 24px;
    }

    .my__header-content__people {
        flex-direction: column;
    }

    .my__header-content__people p {
        margin: 0;
    }

    .my__header-content__input input, 
    .my__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    } 
}

@media screen and (max-width: 490px) {
    .my__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .my__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .my__header-content__input input, 
    .my__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    } 
}